}
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
p {
  margin-top: 0;
}

h1, .h1,
h2, .h2 {
  margin-bottom: 0;
  font-family: "Nunito-Extra-Light", "Helvetica", "Verdana", "Arial", sans-serif;
  font-weight: 200;
  line-height: 1.1;
}

h1,
.h1 {
  font-size: 24px;
}
@media (min-width: 768px) {
  h1,
  .h1 {
    font-size: 28px;
  }
}
@media (min-width: 1440px) {
  h1,
  .h1 {
    font-size: 48px;
  }
}

h2,
.h2 {
  font-size: 20px;
}
@media (min-width: 768px) {
  h2,
  .h2 {
    font-size: 24px;
  }
}
@media (min-width: 1440px) {
  h2,
  .h2 {
    font-size: 36px;
  }
}

a {
  color: #016275;
  text-decoration: none;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  a:hover {
    color: #967d66;
  }
}

*,
::after,
::before {
  box-sizing: border-box;
}

a,
input,
button,
select,
textarea {
  outline: none;
}

svg,
img {
  display: block;
  width: 100%;
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul,
ol,
dl {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

html {
  font-size: 12px;
}
@media (min-width: 768px) {
  html {
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  html {
    font-size: 18px;
  }
}

body {
  height: 200vh;
  font-family: "Nunito-Extra-Light", "Helvetica", "Verdana", "Arial", sans-serif;
  line-height: 1.33;
  color: #161616;
  background-color: #f9f5f1;
}

.main {
  margin-top: 60px;
  padding-top: 20px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .main {
    margin-top: 65px;
    padding-bottom: 75px;
  }
}
@media (min-width: 1440px) {
  .main {
    padding-top: 30px;
    padding-bottom: 110px;
  }
}
@media (min-width: 1920px) {
  .main {
    margin-top: 70px;
    padding-top: 50px;
    padding-bottom: 120px;
  }
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding-right: 60px;
    padding-left: 60px;
    max-width: 1840px;
  }
}

.hidden {
  display: none !important;
}

.no-scroll {
  overflow: hidden;
}

.btn {
  position: relative;
  display: block;
  margin: 0 auto;
  max-width: 180px;
  width: 100%;
  height: 40px;
  font-size: 10px;
  font-family: "Nunito-Semi-Bold", "Helvetica", "Verdana", "Arial", sans-serif;
  color: #016275;
  background-color: transparent;
  border: 1px solid #016275;
  cursor: pointer;
  transition: 0.2s ease;
}
@media (min-width: 1440px) {
  .btn {
    max-width: 240px;
    height: 50px;
    font-size: 12px;
  }
}
@media (any-hover: hover) {
  .btn:hover {
    box-shadow: inset 0 0 0 2em #016275;
    color: #f9f5f1;
  }
}
.btn--bg {
  color: #f9f5f1;
  border: 0;
  background: linear-gradient(278deg, #efe5dc -90.03%, #016275 56.92%);
}
.btn--bg:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(98deg, #efe5dc -90.03%, #016275 56.92%);
  opacity: 0;
  transition: opacity 0.2s ease;
}
@media (any-hover: hover) {
  .btn--bg:hover {
    box-shadow: none;
  }
  .btn--bg:hover:after {
    opacity: 1;
  }
}
.btn--bg span {
  position: relative;
  z-index: 1;
}

.popup {
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .popup {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
@media (min-width: 1920px) {
  .popup {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.popup-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 0;
  height: 0;
  transition-delay: 0.2s;
}
.popup-wrapper.is-open {
  width: 100vw;
  height: 100vh;
  background-color: rgba(150, 125, 102, 0.3);
  transition-delay: unset;
}
.popup__close {
  margin-left: auto;
  margin-bottom: 20px;
  width: 16px;
  height: 16px;
  transition: 0.2s ease;
  cursor: pointer;
}
@media (min-width: 768px) {
  .popup__close {
    margin-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .popup__close {
    margin-bottom: 45px;
  }
}
@media (any-hover: hover) {
  .popup__close:hover {
    transform: rotate(90deg);
  }
}
.popup__container {
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 390px) {
  .popup__container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 768px) {
  .popup__container {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (min-width: 1920px) {
  .popup__container {
    padding-right: 60px;
    padding-left: 60px;
  }
}

.form__group {
  position: relative;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .form__group {
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .form__group {
    margin-bottom: 30px;
  }
}
.form__input {
  padding: 0;
  width: 100%;
  height: 35px;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #a4a4a4;
  color: #161616;
}
@media (min-width: 1440px) {
  .form__input {
    height: 45px;
  }
}

.header {
  transform: translateY(-100%);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  background-color: #f6f1eb;
  box-shadow: 0 3px 20px 0 rgba(87, 79, 69, 0.1);
  transition: 0.2s ease;
}
.header.is-fixed {
  transform: translateY(0);
}
.header__content {
  display: flex;
  align-items: center;
  gap: 20px;
}
.header__content-item:not(:nth-child(2)) {
  flex: 1 1 50%;
}
@media (max-width: 1439px) {
  .header-menu {
    display: none;
  }
}
@media (min-width: 1440px) {
  .header-menu {
    display: flex;
    align-items: center;
    gap: 35px;
    font-size: 12px;
  }
}
@media (min-width: 1920px) {
  .header-menu {
    font-size: 14px;
  }
}
@media (min-width: 1440px) {
  .header-menu__link {
    color: #161616;
  }
}
.header__logo {
  display: block;
  color: #161616;
  width: 75px;
}
@media (min-width: 768px) {
  .header__logo {
    width: 95px;
  }
}
@media (min-width: 1920px) {
  .header__logo {
    width: 115px;
  }
}
.header__logo:hover {
  color: #161616;
}
.header-informers {
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (min-width: 768px) {
  .header-informers {
    gap: 20px;
  }
}
@media (min-width: 1920px) {
  .header-informers:not(.header-informers--left) {
    gap: 25px;
  }
}
.header-informers--right {
  justify-content: flex-end;
}
@media (min-width: 1440px) {
  .header-informers--left {
    gap: 35px;
  }
}
.header-informers__link {
  color: #161616;
}
.header-informers__icon {
  display: block;
  width: 15px;
}
@media (min-width: 768px) {
  .header-informers__icon {
    width: 20px;
  }
}

.language-informer {
  display: flex;
  align-items: center;
  gap: 10px;
}
.language-informer__name {
  font-family: "Nunito", "Helvetica", "Verdana", "Arial", sans-serif;
