.mobile-menu__btn-burger {
  width: 20px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu__btn-burger {
    width: 30px;
  }
}
@media (min-width: 1440px) {
  .mobile-menu__btn-burger {
    display: none;
  }
}
.mobile-menu__btn-burger rect {
  transition: 0.2s ease;
}
.mobile-menu__btn-burger:hover rect:first-child {
  width: 30px;
}
.mobile-menu__btn-burger:hover rect:nth-child(2) {
  width: 20px;
}
.mobile-menu__btn-burger:hover rect:nth-child(3) {
  width: 10px;
}
.mobile-menu__btn-burger:hover rect:nth-child(4) {
  width: 15px;
}
.mobile-menu__btn-burger:hover rect:nth-child(5) {
  width: 25px;
}
