  .mobile-menu__btn-text {
    display: none;
  }
}
@media (min-width: 1440px) {
  .mobile-menu__btn-text {
    display: flex;
    gap: 5px;
    font-size: 12px;
    line-height: normal;
    cursor: pointer;
    transition: 0.2s ease;
  }
}
@media (min-width: 1920px) {
  .mobile-menu__btn-text {
    font-size: 14px;
  }
}
@media (any-hover: hover) {
  .mobile-menu__btn-text:hover {
    color: #967d66;
  }
}
.mobile-menu__btn-text:hover .mobile-menu__btn-text-icon {
  transform: rotate(45deg);
}
@media (min-width: 1440px) {
  .mobile-menu__btn-text-icon {
    width: 10px;
    color: #967d66;
    transition: 0.2s ease;
  }
}
@media (min-width: 1920px) {
  .mobile-menu__btn-text-icon {
    width: 13px;
  }
}
