// Set box-sizing for all elements
*,
::after,
::before {
  box-sizing: border-box;
}

// Reset outline on focus
a,
input,
button,
select,
textarea {
  outline: none;
}

// Set block behavior for img/svg
svg,
img {
  display: block;
  width: 100%;
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: cover;
}

// Lists style
ul,
ol,
dl {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

// Base style
html {
  font-size: 12px;

  @media (min-width: $md) {
    font-size: 14px;
  }

  @media (min-width: $xl) {
    font-size: 18px;
  }
}

body {
  height: 200vh;
  font-family: $font-txt;
  line-height: 1.33;
  color: $txt-clr;
  background-color: $bg-clr;
}

.main {
  margin-top: 60px;
  padding-top: 20px;
  padding-bottom: 50px;

  @media (min-width: $md) {
    margin-top: 65px;
    padding-bottom: 75px;
  }

  @media (min-width: $xl) {
    padding-top: 30px;
    padding-bottom: 110px;
  }

  @media (min-width: $xxl) {
    margin-top: 70px;
    padding-top: 50px;
    padding-bottom: 120px;
  }
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;

  @media (min-width: $md) {
    padding-right: 20px;
    padding-left: 20px;
  }

  @media (min-width: $xl) {
    margin: 0 auto;
    padding-right: 60px;
    padding-left: 60px;
    max-width: 1840px;
  }
}

.hidden {
  display: none!important;
}

.no-scroll {
  overflow: hidden;
}

// Buttons
.btn {
  position: relative;
  display: block;
  margin: 0 auto;
  max-width: 180px;
  width: 100%;
  height: 40px;
  font-size: 10px;
  font-family: $font-btn;
  color: $main-clr;
  background-color: transparent;
  border: 1px solid $main-clr;
  cursor: pointer;
  transition: $transition;

  @media (min-width: $xl) {
    max-width: 240px;
    height: 50px;
    font-size: 12px;
  }

  @media (any-hover: hover) {
    &:hover {
      box-shadow: inset 0 0 0 2em $main-clr;
      color: $txt-bg-clr;
    }
  }

  &--bg {
    color: $txt-bg-clr;
    border: 0;
    background: linear-gradient(278deg, #efe5dc -90.03%, #016275 56.92%);

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(98deg, #efe5dc -90.03%, #016275 56.92%);
      opacity: 0;
      transition: opacity $transition;
    }

    @media (any-hover: hover) {
      &:hover {
        box-shadow: none;

        &:after {
          opacity: 1;
        }
      }
    }

    span {
      position: relative;
      z-index: 1;
    }
  }
}

// Popup
.popup {
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: $md) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @media (min-width: $xxl) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 0;
    height: 0;
    transition-delay: .2s;

    &.is-open {
      width: 100vw;
      height: 100vh;
      background-color: rgba(150, 125, 102, .30);
      transition-delay: unset;
    }
  }

  &__close {
    margin-left: auto;
    margin-bottom: 20px;
    width: 16px;
    height: 16px;
    transition: $transition;
    cursor: pointer;

    @media (min-width: $md) {
      margin-bottom: 25px;
    }

    @media (min-width: $xl) {
      margin-bottom: 45px;
    }

    @media (any-hover: hover) {
      &:hover {
        transform: rotate(90deg);
      }
    }
  }

  &__container {
    padding-right: 15px;
    padding-left: 15px;

    @media (min-width: $xs) {
      padding-right: 20px;
      padding-left: 20px;
    }

    @media (min-width: $md) {
      padding-right: 40px;
      padding-left: 40px;
    }

    @media (min-width: $xxl) {
      padding-right: 60px;
      padding-left: 60px;
    }
  }
}

// Form
.form {
  &__group {
    position: relative;
    margin-bottom: 15px;

    @media (min-width: $md) {
      margin-bottom: 20px;
    }

    @media (min-width: $xl) {
      margin-bottom: 30px;
    }
  }

  &__input {
    padding: 0;
    width: 100%;
    height: 35px;
    background: transparent;
    border: 0;
    border-bottom: 1px solid $gray;
    color: $txt-clr;

    @media (min-width: $xl) {
      height: 45px;
    }
  }
}
