.header {
  transform: translateY(-100%);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  background-color: $header-bg-clr;
  box-shadow: 0 3px 20px 0 rgba(87, 79, 69, .10);
  transition: $transition;

  &.is-fixed {
    transform: translateY(0);
  }

  // header grid
  &__content {
    display: flex;
    align-items: center;
    gap: 20px;

    &-item:not(:nth-child(2)) {
      flex: 1 1 50%;
    }
  }

  // desktop menu
  &-menu {
    @media (max-width: $max-xl) {
      display: none;
    }

    @media (min-width: $xl) {
      display: flex;
      align-items: center;
      gap: 35px;
      font-size: 12px;
    }

    @media (min-width: $xxl) {
      font-size: 14px;
    }

    &__link {
      @media (min-width: $xl) {
        color: $txt-clr;
      }
    }
  }

  // logo
  &__logo {
    display: block;
    color: $txt-clr;
    width: 75px;

    @media (min-width: $md) {
      width: 95px;
    }

    @media (min-width: $xxl) {
      width: 115px;
    }

    &:hover {
      color: $txt-clr;
    }
  }

  // informers
  &-informers {
    display: flex;
    align-items: center;
    gap: 15px;

    @media (min-width: $md) {
      gap: 20px;
    }

    @media (min-width: $xxl) {
      &:not(.header-informers--left) {
        gap: 25px;
      }
    }

    &--right {
      justify-content: flex-end;
    }

    &--left {
      @media (min-width: $xl) {
        gap: 35px;
      }
    }

    &__link {
      color: $txt-clr;
    }

    &__icon {
      display: block;
      width: 15px;

      @media (min-width: $md) {
        width: 20px;
      }
    }
  }
}

// Language
.language-informer {
  display: flex;
  align-items: center;
  gap: 10px;

  &__name {
    font-family: $font-title;
    font-size: 10px;
    line-height: 1;
  }

  &--mob {
    @media (min-width: $xl) {
      display: none;
    }
  }

  &--desk {
    @media (max-width: $max-xl) {
      display: none;
    }
  }
}

// Search
.search-informer {
  &--desk {
    @media (max-width: $max-xl) {
      display: none;
    }
  }
}

// Profile
.profile-informer {
  position: relative;

  &__is-logged {
    position: absolute;
    right: -1px;
    bottom: -1px;
    color: $txt-clr;

    @media (min-width: $md) {
      width: 12px;
      height: 12px;
    }
  }
}

// Cart
.cart-informer {
  position: relative;

  &__counter {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70%;
    height: 70%;
    font-family: $font-title;
    font-size: 6px;
    color: $txt-clr;
    border-radius: 50%;
    background: $header-bg-clr;
    border: 1px solid $txt-clr;
    line-height: 1;

    @media (min-width: $md) {
      font-size: 8px;
    }
  }
}
